/*! 地区筛选 css */
.select-area {
  padding: 0 @30px;
  min-height: 100%;
  .items {
  }
  .item {
    &.active {
      .icon {
        transform: rotate(90deg);
      }
      ul {
        display: block;
      }
    }
    .icon {
      width: @12px;
      height: @20px;
      margin-right: @14px;
      background-image: url(../images/icon/icon-sjx.png);
      transition: all 0.3s;
    }
    .title {
      height: @100px;
      line-height: @100px;
      font-size: @30px;
      color: #333;
      border-bottom: 1px solid #ddd;
    }
    ul {
      display: none;
      padding-left: @30px;
      .title {
        color: #666;
        height: @80px;
        font-size: @28px;
        line-height: @80px;
      }
    }
  }
}
