/*! 修改手机号 css */
.edit-phoneNumber {
  padding: 0;
  min-height: 100%;
  background-color: #fff;
  text-align: center;
  font-size: 0;
  .img-box {
    padding: @182px 0 @30px;
  }
  img {
    width: @294px;
    height: @240px;
    margin: auto;
  }
  p {
    font-weight: 500;
    font-size: @32px;
    line-height: 1;
    color: #333;
    line-height: 1;
    padding-bottom: @34px;
    span {
      color: rgb(2, 125, 247);
      font-size: @32px;
    }
  }
  span {
    display: inline-block;
    color: #999;
    font-size: @24px;
    line-height: 1;
  }

  .icon {
    width: @30px;
    height: @30px;
    margin-right: @20px;
  }
  .icon-hook {
    background-image: url(../images/change-phone-number/icon-hook.png);
  }
  .icon-cross {
    background-image: url(../images/change-phone-number/icon-cross.png);
  }
  .btn-box {
    padding: @40px @60px;
  }
  .btn {
    background-color: #f5f9ff;
    margin-bottom: @30px;
    height: @80px;
    line-height: @80px;
    span {
      font-size: @32px;
      color: #027df7;
    }
    &:last-of-type {
      background-color: #f9f7f0;
      span {
        color: #c89f51;
      }
    }
  }
}
