/*! 智能咨询 cs改手机号-手机未换号1 css */
// 发送验证提示
.send-verification-code-tip-box {
  text-align: center;
  font-size: 0;
  padding: @60px @30px 0;
  > div {
    border-bottom: 1px solid #ddd;
  }
  p {
    color: #333;
    font-size: @40px;
    line-height: 1;
    padding-top: @40px;
    padding-bottom: @70px;
  }
  span {
    display: inline-block;
    line-height: 1;
    color: #999999;
    font-size: @30px;
  }
}
.input-wrapper {
  padding: 0 @30px;
  .input-box.type-2 {
    height: @100px;
    border-bottom: 1px solid #ddd;
    position: relative;
    .btn {
      position: absolute;
      top: 50%;
      right: 0;
      width: auto;
      height: auto;
      background-color: transparent;
      color: #999;
      font-size: @28px;
      font-weight: normal;
      line-height: 1;
      transform: translateY(-50%);
    }
    .code {
      width: @138px;
      height: @76px;
      border-radius: @4px;
      background-color: #eee;
    }
    input {
      padding: 0 @160px 0 0;
      font-size: @28px;
    }
  }
}
.no-change-phone-number {
  padding: 0;
  min-height: 100%;
  background-color: #fff;
  label {
    min-width: @150px;
    line-height: 1;
    white-space: normal;
    font-size: @28px;
    color: #333;
  }
  .btn-box {
    padding: @40px @60px;
    &.result {
      padding: @80px @60px;
    }
  }
  .select-box {
    position: relative;
    border-top: 1px solid #ddd;
    border-bottom: 1px solid #ddd;
    select {
      width: 100%;
      font-size: @28px;
      color: #333;
    }
    .icon {
      position: absolute;
      top: 50%;
      right: 0;
      transform: translateY(-50%);
      width: @24px;
      height: @14px;
      background-image: url(../images/icon-arrow-down.png);
    }
  }
  .step2 {
    input {
      text-align: right;
      color: #999;
      font-size: @28px;
    }
  }
  .img-box {
    padding: 0;
    width: 100%;
    padding-top: @150px;
    img {
      display: block;
      width: @202px;
      height: @150px;
      margin: 0 auto;
    }
  }
  .text {
    line-height: 1;
    text-align: center;
    padding-top: @10px;
    p {
      font-size: @36px;
      padding-bottom: @30px;
      font-weight: 500;
      color: #333;
    }
    span {
      display: block;
      color: #999;
      font-size: @28px;
    }
    b {
      color: rgb(0, 124, 247);
      font-weight: normal;
    }
  }
  .before-line-box {
    span {
      line-height: @90px;
    }
  }
  .slide-yz-box {
    padding: 0 @30px;
    text-align: center;
    margin-top: @20px;
    > div {
      background-color: #f4f9fe;
      color: #007cf7;
      line-height: @80px;
      font-size: @28px;
    }
  }
}
.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.1);
  height: 100%;
  .content {
    width: @440px;
    height: @180px;
    border-radius: @10px;
    position: absolute;
    top: @176px;
    left: 50%;
    transform: translateX(-50%);
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 222;
    padding-top: @34px;
    align-items: flex-start;
    .icon {
      font-size: @60px;
      color: #fff;
      margin: 0 auto;
      margin-bottom: @20px;
    }
    div {
      width: 100%;
      text-align: center;
      color: #fff;
      font-size: @28px;
      line-height: 1;
    }
  }
}
