/*! 服务地图 css */
.serve-map {
  padding: 0;
  height: 100%;
  .tabs {
    padding: 0 @96px;
    position: static;
    border-bottom: 1px solid #f7f8f9;
  }
  .flex-1 {
    overflow: auto;
  }
  .before-line-box {
    border-top: @18px solid #f7f8f9;
  }
  .content {
    width: 100%;
    padding: 0 @30px;
    margin-top: -@30px;
    li {
      width: 100%;
      height: @180px;
      padding-top: @30px;
      border-bottom: 1px solid #ddd;
      position: relative;
    }
    h3 {
      font-size: @30px;
      line-height: 1;
      color: #333;
    }
    .address {
      font-size: @28px;
      line-height: 1;
      padding: @20px 0;
      color: #999;
    }
    .phone {
      font-size: @28px;
      line-height: 1;
      color: #999;
    }
  }

  .icon-address {
    width: @20px;
    height: @30px;
    background-image: url(../images//icon/icon-address-2.png);
    margin-right: @10px;
  }
  .icon-phone {
    width: @24px;
    height: @24px;
    background-image: url(../images//icon/icon-phone.png);
    margin-right: @6px;
  }
  .icon-arrow-right-grey-1424 {
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
  }
}
