/*! 我的办件记录 css */
.record {
  padding: 0;
  height: 100%;
  .tabs {
    padding: 0 @54px;
    position: static;
    border-bottom: 1px solid #f7f8f9;
  }
  .flex-1 {
    overflow: auto;
  }
  .main {
    border-top: @18px solid #f7f8f9;
    background-color: #fff;
    overflow: auto;
    padding: @76px 0 0 @30px;
    li {
      padding: 0 @30px 0 @24px;
      margin-bottom: @30px;
      position: relative;
      &::before {
        content: "";
        width: 1px;
        background-color: #ddd;
        height: @112px;
        position: absolute;
        top: 50%;
        left: 0;
        transform: translateY(-50%);
      }
      &:last-of-type {
        &::before {
          background-color: transparent;
        }
      }
    }
  }

  .card {
    position: relative;
    box-shadow: 0 0 @4px @4px #f7f8f9;
    height: @130px;
    padding: @30px 0 0 @20px;
    top: -@56px;
    h3 {
      font-weight: 500;
      font-size: @30px;
      color: #333;
      line-height: 1;
      padding-bottom: @20px;
      position: relative;
      &::before {
        position: absolute;
        left: -@56px;
        top: 0;
        content: "";
        width: @24px;
        height: @24px;
        border: @8px solid #007cf7;
        background-color: #fff;
        border-radius: 50%;
      }
    }
    .time {
      color: #999;
      font-size: @24px;
      line-height: 1;
    }
    .status {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      right: @20px;
      font-size: @24px;
      line-height: 1;
      color: #6ddd04;
      background-color: #e0fbc3;
      border-radius: @4px;
      padding: @12px @24px;
      &.ok {
        color: #057ff7;
        background-color: #d7f0fd;
      }
      &.not {
        color: #fe0a5f;
        background-color: #fff0f5;
      }
    }
  }
}
