/*! css */
.yz {
  padding: 0;
  min-height: 100%;
  .icon-hint {
    width: @32px;
    height: @32px;
    background-image: url(../images/icon/icon-hint.png);
    margin-right: @10px;
  }
  .hint {
    height: @100px;
    font-size: @32px;
    padding: 0 @30px;
    background-color: #f4f9fe;
    span {
      color: #444;
    }
  }
  ul {
    padding-top: @30px;
  }
  li {
    margin-bottom: @30px;
    padding: 0 @30px;
  }
  .card {
    box-shadow: 0 0 @4px @4px #f7f8f9;
    height: @260px;
    padding-right: @40px;
    border-radius: @10px;
    .left {
      width: @228px;
    }
    .right {
      flex: 1;
    }
    img {
      height: @116px;
      width: @132px;
      &.img-info {
        height: @120px;
        width: @132px;
      }
      &.img-qqh {
        height: @146px;
        width: @116px;
        color: #fff;
      }
    }
    h3 {
      font-size: @32px;
      color: #333;
      line-height: 1;
      padding-bottom: @20px;
    }
    p {
      color: #999;
      font-size: @28px;
      line-height: @40px;
    }
    a {
      padding-top: @20px;
      display: inline-block;
      color: #007cf7;
      font-size: @22px;
      line-height: 1;
    }
  }
  .cell-wrapper {
    padding: 0 @30px;
  }
  .cell {
    height: @100px;
    border-bottom: 1px solid #ddd;
    color: #333;
    font-size: @28px;
    span {
      color: #999;
    }
  }
}
