/*! 我的预约 css */
// tabs
.tabs {
  width: 100%;
  left: 0;
  top: 0;
  position: fixed;
  padding: 0 @38px 0 @76px;
  border-bottom: @20px solid #f7f8f9;
  background-color: #fff;
  z-index: 999;
  li {
    span {
      display: inline-block;
      position: relative;
      line-height: @96px;
      color: #333;
      &::after {
        content: "";
        width: @52px;
        height: @6px;
        background-color: #333;
        border-radius: 20em;
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        bottom: @20px;
        opacity: 0;
      }
      &::before {
        content: "";
        width: @16px;
        height: @16px;
        background-color: #ff3149;
        border-radius: 50%;
        position: absolute;
        top: @20px;
        right: -@20px;
        opacity: 0;
      }
    }
    &.active {
      span {
        font-weight: 500;
        &::before {
          opacity: 1;
        }
        &::after {
          opacity: 1;
        }
      }
    }
  }
}
.my-appointment {
  padding: 0;
  height: 100%;
  background-color: #fff;
  .tabs {
    position: static;
    border-bottom: 1px solid #f7f8f9;
    padding: 0 @56px;
  }
  .precess {
    overflow: auto;
    ul {
      border-top: @18px solid #f7f8f9;
      padding-top: @20px;
    }
    li {
      padding-left: @40px;
      &:last-of-type {
        .card {
          border: 0;
        }
      }
    }
    .time {
      padding-left: @24px;
      color: #999;
      line-height: 1;
      position: relative;
      font-size: @24px;
      line-height: 1;
      &::after {
        content: "";
        position: absolute;
        left: 0;
        width: @24px;
        height: @24px;
        background-color: #fff;
        border-radius: 50%;
        border: @6px solid #007cf7;
        top: 50%;
        transform: translate(-50%, -50%);
      }
    }
    .card {
      height: @194px;
      padding: 0 @30px 0 @24px;
      // background-color: #f00;
      margin: @10px 0;
      border-left: 1px solid #ddd;
    }
    .inner {
      width: 100%;
      height: @170px;
      background-color: #fff;
      box-shadow: 0 0 @8px @8px #f7f8f9;
      border-radius: @6px;
      padding: @30px @20px 0;
      position: relative;
      > div {
        padding-top: @20px;
      }
      span {
        color: #999;
        line-height: 1;
        font-size: @24px;
      }
    }
    h3 {
      color: #333;
      line-height: 1;
      font-weight: 500;
      font-size: @30px;
    }
    .status {
      position: absolute;
      top: @70px;
      right: @24px;
      padding: @6px @12px !important;
      background-color: #e0fbc3;
      font-size: @20px;
      color: #6ddd04;
      text-align: center;
      line-height: 1;
      border-radius: @4px;
      &.ok {
        background-color: #d7f0fd;
        color: #0883f7;
      }
      &.not {
        background-color: #fff0f5;
        color: #fe0a5f;
      }
    }
    .icon-address {
      width: @20px;
      height: @24px;
      background-image: url(../images/icon/icon-address.png);
      margin-right: @14px;
    }
    .icon-house {
      width: @24px;
      height: @24px;
      background-image: url(../images/icon/icon-house.png);
      margin-right: @10px;
    }
  }
}
