/*! 服务评价 css */
.serve-evaluate {
  padding: 0;
  height: 100%;
  .tabs {
    border: 1px solid #f7f8f9;
    position: static;
    padding: 0 @102px;
  }
  .flex-1 {
    overflow: auto;
  }
  .main {
    border-top: @18px solid #f7f8f9;
    padding: 0 @30px;
    background-color: #fff;
    li {
      height: @136px;
      border-bottom: 1px solid #ddd;
    }
    p {
      padding-bottom: @16px;
    }
    .btn {
      width: @140px;
      height: @44px;
      line-height: @40px;
      border: 1px solid #1a90fe;
      background-color: transparent;
      color: #007cf7;
      border-radius: 20em;
      font-size: @24px;
    }
  }

  // 2
  .info {
    padding: 0 @30px;
    padding-top: @20px;
  }
  .row {
    height: @70px;
    span,
    p {
      font-size: @28px;
      color: #333;
    }
  }
  .before-line-box {
    position: relative;
    padding-top: @30px;
    span {
      line-height: @40px;
    }
    .status {
      position: absolute;
      background-color: #e8f3fe;
      color: #0880f7;
      border-radius: @4px;
      font-size: @24px;
      padding: @8px @16px;
      top: @30px;
      right: @30px;
    }
  }
  .mark {
    margin-top: @20px;
    border-top: 1px solid #ddd;
    border-bottom: 1px solid #ddd;
    height: @100px;
    span {
      font-size: @24px;
      color: #999;
    }
  }

  .icon-star {
    width: @30px;
    height: @30px;
    margin-right: @20px;
    background-image: url(../images/icon/icon-star.png);
    &.active {
      background-image: url(../images/icon/icon-star-active.png);
    }
  }
  .textarea-box {
    padding: 0 @30px;
  }
  textarea {
    width: 100%;
    height: @280px;
    background-color: transparent;
    padding: @20px 0;
    border: 0;
    font-size: @28px;
    line-height: 1.4;
    border-bottom: 1px solid #ddd;
  }
}
