/*! 在线缴费 css */
.online-payment {
  padding: 0;
  min-height: 100%;
  background-color: #fff;
  .info {
    padding-top: @68px;
    text-align: center;
    img {
      width: @86px;
      height: @86px;
      margin: auto;
    }
    span {
      display: inline-block;
      font-size: @56px;
      color: #333;
      padding-top: @30px;
      line-height: 1;
      font-weight: 500;
      &::before {
        content: "¥";
        font-size: @42px;
        line-height: 1;
        padding-right: @16px;
      }
    }
    p {
      font-size: @30px;
      line-height: 1;
      padding-top: @20px;
      color: #333;
    }
    &.result {
      padding-top: @154px;
      img {
        width: @202px;
        height: @150px;
      }
      p {
        color: #2cc70f;
        font-size: @36px;
        font-weight: 500;
      }
      .name {
        color: #999;
        font-size: @28px;
        padding-top: @50px;
        font-weight: normal;
      }
    }
  }
  .cell-box {
    padding: 0 @30px;
    padding-top: @70px;
  }
  .cell {
    height: @110px;
    line-height: 1;
    font-size: @28px;
    border-bottom: 1px solid #ddd;
    color: #333;
    .left {
      color: #999;
    }
  }
  .btn-box {
    padding: @60px;
    &.result {
      padding-top: @100px;
    }
  }
  .popup {
    display: none;
    background-color: rgba(0,0,0,.3);
    .content {
      width: 90%;
      top: 50%;
      height: auto;
      transform: translate(-50%, -50%);
      background-color: #fff;
      color: #333;
      padding: @30px;
    }
    .title {
      color: #333;
      font-size: @30px;
      font-weight: 500;
    }
    p {
      width: 100%;
      display: block;
      color: #333;
      text-align: center;
      font-weight: 500;
      padding-top: @40px;
    }
    .num {
      font-size: @60px;
      &::before {
        content: '¥';
        padding-right: @10px;
        font-size: @40px;
      }
    }
    i {
      color: #333;
    }
    .type {
      color: #666;
      margin-top: @40px;
      height: @80px;
      .right {
        color: #333;
      }
    }
  }
}
